import Vue from 'vue';
import Vuex from 'vuex';
import router from '@/router';
import clone from '@/lib/clone';
import createId from '@/lib/createId';

Vue.use(Vuex);


// dataIcon: [
//   {type: '-', name: 'xiezi', text: ''},
//   {type: '-', name: 'jiaotong', text: ''},
//   {type: '-', name: 'meirong', text: ''},
//   {type: '-', name: 'riyong', text: ''},
//   {type: '-', name: 'shiwu', text: ''},
//   {type: '-', name: 'yifu', text: ''},
//   {type: '-', name: 'zaocan', text: ''},
//   {type: '-', name: 'shuiguo', text: ''},
//   {type: '+', name: 'gongzi', text: ''},
//   {type: '+', name: 'jiangjin', text: ''},
//   {type: '+', name: 'lijing', text: ''},
//   {type: '+', name: 'gupiao', text: ''},
// ],


const store = new Vuex.Store({
    state: {
      dataIcon: [],
      tagIcon: [],
      recordData: [],
    } as rootState,
    mutations: {
      setRead(state) {       //localStorage    --- window 
        window.localStorage.setItem('recordTagList', JSON.stringify(state.tagIcon));
      },
      getSave(state) {        //localStorage
        state.tagIcon = JSON.parse(window.localStorage.getItem('recordTagList') || '[]');
      },
      crateTag(state, add: RecordItem) {     //localStorage
        const add2: RecordItem = JSON.parse(JSON.stringify(add));
        store.commit('getSave');
        const BB = state.tagIcon.map(item => item.text);
        if (add.text === '') {
          window.alert(',');
          return;
        } else if (BB.indexOf(add.text) >= 0) {
          window.alert('');
          return;
        } else {
          state.tagIcon.push(add2);
          window.localStorage.setItem('recordTagList', JSON.stringify(state.tagIcon));
          router.push('/');
        }
      },
      crateTagTwo(state, add: RecordItem) {
        const add2: RecordItem = JSON.parse(JSON.stringify(add));
        store.commit('getSave');
        state.tagIcon.push(add2);
        window.localStorage.setItem('recordTagList', JSON.stringify(state.tagIcon));
      },
      fetchTags() {
        store.commit('crateTagTwo', {type: '+', name: 'gongzi', text: ''},
        );
        store.commit('crateTagTwo', {type: '+', name: 'gupiao', text: ''},
        );
        store.commit('crateTagTwo', {type: '+', name: 'jiangjin', text: ''},
        );
        store.commit('crateTagTwo', {type: '+', name: 'lijing', text: ''},
        );
        store.commit('crateTagTwo', {type: '-', name: 'jiaotong', text: ''});
        store.commit('crateTagTwo', {type: '-', name: 'riyong', text: ''});
        store.commit('crateTagTwo', {type: '-', name: 'shiwu', text: ''});
        store.commit('crateTagTwo', {type: '-', name: 'yifu', text: ''});
        store.commit('crateTagTwo', {type: '-', name: 'zhusu', text: ''});


      },
      removeTag(state, value) {     //localStorage
        let index = 0;
        store.commit('getSave');
        for (let i = 0; i < state.tagIcon.length; i++) {
          if (state.tagIcon[i].text === value) {
            index = i;
          }
        }
        state.tagIcon.splice(index, 1);
        window.localStorage.setItem('recordTagList', JSON.stringify(state.tagIcon));
      },

      //record

      read(state) {  //
        state.recordData = JSON.parse(window.localStorage.getItem('recordList') || '[]');
      },

      save(state) {   //
        window.localStorage.setItem('recordList', JSON.stringify(state.recordData));
      },
      createRecord(state, record: RecordItem) {   //
        // record.createAt = new Date().toISOString();
        record.id = createId();
        const recordCopy: RecordItem = clone(record);
        state.recordData.push(recordCopy);
        window.localStorage.setItem('recordList', JSON.stringify(state.recordData));
      },


      removeRecord(state, value: number) {
        let index = 0;
        store.commit('read');
        for (let i = 0; i < state.recordData.length; i++) {
          if (state.recordData[i].id === value) {
            index = i;
          }
        }
        console.log(index);
        state.recordData.splice(index, 1);
        window.localStorage.setItem('recordList', JSON.stringify(state.recordData));
      },
    }
  }
);

export default store;